* {
  box-sizing: border-box;
}

body {
  background-color: #3c7b84;
  font: 14px / 20px Lato, Arial, sans-serif;
}

.container {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding-bottom: 0;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

.input-wrap {
  width: 400px;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.input-text {
  width: 180px;
  color: #fff;
  letter-spacing: .05em;
  pointer-events: none;
  padding: 20px 10px 10px;
  font-size: 1.5em;
}

#search-box {
  width: 100%;
  box-shadow: none;
  color: #010102;
  letter-spacing: .05em;
  z-index: 10;
  font-size: 1.3em;
  font: inherit;
  max-width: 320px;
  background: #aecced;
  border-color: #212121;
  border-radius: 5px;
  outline: none;
  margin-bottom: 16px;
  padding: 8px;
  transition: all .5s;
}

.country-info {
  min-width: 300px;
  background-color: rgba(233, 241, 248, .635);
  justify-content: flex-start;
  margin-top: 40px;
  display: flex;
}

.country-box-list {
  padding-left: 10px;
  padding-right: 10px;
  list-style: none;
}

.country-box-list li {
  letter-spacing: .03em;
  color: #212121;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 19px;
}

.country-box-name {
  text-align: center;
  margin: 0;
}

.coutry-box-span {
  margin-right: 5px;
  font-weight: 700;
}

.country-box-top {
  align-items: center;
  gap: 20px;
  display: flex;
}

.country-list {
  padding-left: 0;
  list-style: none;
}

.country-list img {
  width: 40px;
}

.country-list li {
  background-color: rgba(233, 241, 248, .39);
  padding: 10px;
  font-size: 12px;
  font-weight: 700;
}

